import React, { useState } from 'react';
import { Input } from 'antd';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

function Tracuu() {
    const [cmnd, setCmnd] = useState('');
    const [phone, setPhone] = useState('');

    const validatePhone = (phone) => {
        const phoneRegex = /^0\d{9}$/;
        return phoneRegex.test(phone);
    };

    const validateCmnd = (cmnd) => {
        const cmndRegex = /^\d{9,12}$/;
        return cmndRegex.test(cmnd);
    };

    const notify = () => {
        if (!cmnd && !phone) {
            toast.error('Anh/chị vui lòng nhập số CMND/CCCD hoặc Số điện thoại để tra cứu kết quả ứng tuyển', {
                position: "top-right",
                autoClose: 3000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        } else if (cmnd && !validateCmnd(cmnd)) {
            toast.error('Số CMND/CCCD không hợp lệ, vui lòng kiểm tra lại', {
                position: "top-right",
                autoClose: 3000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        } else if (phone && !validatePhone(phone)) {
            toast.error('Số điện thoại không hợp lệ, vui lòng kiểm tra lại', {
                position: "top-right",
                autoClose: 3000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        } else {
            console.log('Proceed with search');
        }
    };

    return (
        <div className="job-result__container ">
            <div className="job-result__wapper">
                <div className="job-result-search__header">
                    <h1 className="job-result__title">Tra cứu kết quả ứng tuyển</h1>
                    <div className="job-result__sub-title">Anh/chị vui lòng nhập số CMND/CCCD hoặc Số điện thoại để tra cứu kết quả ứng tuyển</div>
                </div>
                <div className="job-result-search__form">
                    <div className="job-result-form__control col-12 p-0">
                        <div className="input-title">CMND/CCCD</div>
                        <Input
                            placeholder="Nhập số CMND/CCCD"
                            autoComplete="off"
                            className="job-result-input"
                            size='large'
                            value={cmnd}
                            onChange={(e) => setCmnd(e.target.value)}
                        />
                    </div>
                    <div className="job-result-form__control col-12 p-0">
                        <div className="input-title">Số điện thoại</div>
                        <Input
                            placeholder="Nhập số điện thoại"
                            autoComplete="off"
                            className="job-result-input"
                            size='large'
                            value={phone}
                            onChange={(e) => setPhone(e.target.value)}
                        />
                    </div>
                    <div className="job-result-form__btn-submit" onClick={notify}>Tra cứu kết quả</div>
                </div>
            </div>
            <ToastContainer />
        </div>
    );
}

export default Tracuu;
